










import Vue from 'vue';
import Component from 'vue-class-component';
import { Getter, Action } from 'vuex-class';
import FnNavBar from '../../nmb/components/FnNavBar.vue';

@Component({
  components: { FnNavBar }
})
export default class Home extends Vue {
  msg: string = '这是测试内容';

  @Getter('demandLoading') demandLoading?: boolean;
  @Getter('alldemand') alldemand?: any;
  @Getter('taskList') taskList?: any;

  @Action('getAllDemandApi') getAllDemandApi?: Function;
  @Action('geTask') geTask?: Function;

  created() {
    // 传参
    if (this.getAllDemandApi) this.getAllDemandApi({stateDemand: -1});
    // 不传参
    if (this.getAllDemandApi) this.getAllDemandApi();
    if (this.geTask) this.geTask({ key: '13213', name: 'sdafafa' });
  }
}



